const TodoItem = (props)=>{
    console.log('fsf',props.item)
    
    const labelClass = () =>{
        if(!props.item.isCheckedVal){
            return 'default';
        }
        else{
            return 'checked';
        }
    }
   
    return(
        <div>
                <div>
                    <input type="checkbox" name="checktem" onClick={()=>props.checked(props.item.id)} />
                    <label className={`lbl ${labelClass()}`}>{props.item.value}</label>
                </div>
            <style jsx>{`
                .lbl .default{
                    text-decoration: none;
                }
                .lbl .checked{
                    text-decoration: line-through;
                }
            `}</style>
        </div>
    )
    
}

export default TodoItem